
#Post {
  padding-bottom:70px;
}
.content{  
  margin:20px 0;
  border-bottom:1px solid #dfdfdf;
}
.post-content{
  line-height: 30px;
}
.comment{
  margin:0 10px;
}
.commentEntry{
  padding: 20px 0;
  background-color: white;
  margin-bottom: 10px;
}
.floor {
  padding-right: 5px;
  border-right:solid 1px gray;
}
.info {
  font-size:12px;
  opacity: .45;
}
.topic {
  background-color:rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.65);
  font-size: 0.8125em;
  margin: 8px;
  padding: 8px 16px;
  border-radius: 4px;
}
pre {
white-space: pre-wrap;
word-wrap: break-word;
}




