
.slide-fade-enter-active {
  -webkit-transition: all .5s ;
  transition: all .5s ;
}
.slide-fade-leave-active {
  -webkit-transition: all .3s ;
  transition: all .3s ;
}
.slide-fade-enter, .slide-fade-leave-to{
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  opacity: 0;
}



