
.vux-demo {
  text-align: center;
}
.logo {
  width: 100px;
  height: 100px
}
.gender {
}
.more {
  margin-top:20px;
  text-align: center;
  opacity:.35;
}
#article {
  padding-bottom:70px;
}
.title {
  color: rgb(38, 38, 38);
  font-size: 17px;
  font-weight: bold;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 0px 0px 8px;
}
.excerpt {    
  font-size: 13px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 0px;
}
.forumName {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
  opacity: .65;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.forumName::after{
  content: "\FF0E";
}
.school {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.post {
  background-color: white;
  border-bottom: 1px solid #dedede;
  padding:15px 10px;
  margin:0 0px;
}
.post:active{
  background-color:rgba(255,192,203,.3);
}



